<template>
  <div class="container top-0 position-sticky z-index-sticky">
    <div class="row">
      <div class="col-12">
        <navbar
          is-blur="blur blur-rounded my-3 py-2 start-0 end-0 mx-4 shadow"
          btn-background="bg-gradient-primary"
          :dark-mode="true"
        />
      </div>
    </div>
  </div>
  <main class="mt-4 main-content">
    <section>
      <div class="page-header min-vh-100">
        <div class="container">
          <div class="row">
            <div
              class="col-xl-4 col-lg-5 col-md-7 d-flex flex-column mx-lg-0 mx-auto"
            >
              <div class="card card-plain">
                <div class="card-header pb-0 text-left">
                  <h4 class="font-weight-bolder">Registrarse</h4>
                  <p class="mb-0">Ingresa tu usuario y contraseña para registrarte.</p>
                </div>
                <div class="card-body pb-3">
                  <form role="form">
                    <label>Nombre</label>
                    <soft-input
                      id="name"
                      type="text"
                      placeholder="Ingresa tu nombre"
                      aria-label="Name"
                    />
                    <label>Usuario</label>
                    <soft-input
                      id="email"
                      type="text"
                      placeholder="Ingresa tu usuario"
                      aria-label="Email"
                    />
                    <label>Contraseña</label>
                    <soft-input
                      id="password"
                      type="password"
                      placeholder=""
                      aria-label="Password"
                    />
                    <soft-checkbox
                      id="flexCheckDefault"
                      class="font-weight-light"
                      name="terms"
                      checked
                    >
                      Estoy de acuero con los
                      <a href="#" class="text-dark font-weight-bolder"
                        >Terminos y condiciones</a
                      >
                    </soft-checkbox>
                    <div class="text-center">
                      <soft-button
                        color="primary"
                        variant="gradient"
                        full-width
                        class="w-100 mt-4 mb-0"
                        >Registrarte</soft-button
                      >
                    </div>
                  </form>
                </div>
                <div class="card-footer text-center pt-0 px-sm-4 px-1">
                  <p class="mb-4 mx-auto">
                    ¿Ya tienes una cuenta existente?
                    <router-link
                      :to="{ name: 'Signin Illustration' }"
                      class="font-weight-bold"
                      >Iniciar sesión
                    </router-link>
                  </p>
                </div>
              </div>
            </div>
            <div
              class="col-6 d-lg-flex d-none h-100 my-auto pe-0 position-absolute top-0 end-0 text-center justify-content-center flex-column"
            >
              <div
                class="position-relative bg-gradient-primary h-100 m-3 px-7 border-radius-lg d-flex flex-column justify-content-center"
              >
                <img
                  src="../../../assets/img/shapes/pattern-lines.svg"
                  alt="pattern-lines"
                  class="position-absolute opacity-4 start-0"
                />
                <div class="position-relative">
                  <img
                    class="max-width-500 w-100 position-relative z-index-2"
                    src="../../../assets/img/illustrations/rocket-white.png"
                    alt="rocket"
                  />
                </div>
                <h4 class="mt-5 text-white font-weight-bolder">
                  Tu viaje comienza aquí
                </h4>
                <p class="text-white">
                  Así como se necesita una empresa para sostener un producto, se necesita una comunidad para sostener un protocolo.
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  </main>
</template>

<script>
import Navbar from "@/examples/PageLayout/Navbar.vue";
import SoftInput from "@/components/SoftInput.vue";
import SoftCheckbox from "@/components/SoftCheckbox.vue";
import SoftButton from "@/components/SoftButton.vue";
const body = document.getElementsByTagName("body")[0];

import { mapMutations } from "vuex";
export default {
  name: "SigninIllustration",
  components: {
    Navbar,
    SoftInput,
    SoftCheckbox,
    SoftButton,
  },
  created() {
    this.toggleEveryDisplay();
    this.toggleHideConfig();
    body.classList.remove("bg-gray-100");
  },
  beforeUnmount() {
    this.toggleEveryDisplay();
    this.toggleHideConfig();
    body.classList.add("bg-gray-100");
  },
  methods: {
    ...mapMutations(["toggleEveryDisplay", "toggleHideConfig"]),
  },
};
</script>
