<template>
  <div class="container-fluid">
    <div class="row">
      <div class="mx-auto col-lg-12 col-12">
        <div class="mt-4 card card-body">
          <h6 class="mb-0">Información básica</h6>
          <hr class="my-3 horizontal dark" />
          <div class="row">
            <div class="col-4">
              <div>
                  <label >Conductor(<span class="text-danger">*</span>)</label>
                    <div>
                    <VueMultiselect
                      v-model="trip.driver"
                      :options="options"
                      :searchable="true"
                      :close-on-select="false"
                       placeholder="Selecciona una opción..."
                       label="name" 
                       deselectLabel="Presiona para remover"
                       selectLabel="Presiona para seleccionar"
                       selectedLabel="Seleccionado"
                      >
                      <template #noResult>
                        Elemento no encontrado
                      </template>
                    </VueMultiselect>
                  </div>
              </div>
            </div>
            <div class="col-4">
              <label>Unidad(<span class="text-danger">*</span>)</label>
                  <VueMultiselect
                    v-model="trip.bus"
                    :options="options"
                    :searchable="true"
                    :close-on-select="false"
                      placeholder="Selecciona una opción..."
                      label="name" 
                      deselectLabel="Presiona para remover"
                      selectLabel="Presiona para seleccionar"
                      selectedLabel="Seleccionado"
                    >
                    <template #noResult>
                      Elemento no encontrado
                    </template>
                  </VueMultiselect>
            </div>
            <div class="col-4">
              <label>Ruta(<span class="text-danger">*</span>)</label>
                <VueMultiselect
                  v-model="trip.route"
                  :options="options"
                  :searchable="true"
                  :close-on-select="false"
                    placeholder="Selecciona una opción..."
                    label="name" 
                    deselectLabel="Presiona para remover"
                    selectLabel="Presiona para seleccionar"
                    selectedLabel="Seleccionado"
                  >
                  <template #noResult>
                    Elemento no encontrado
                  </template>
                </VueMultiselect>
            </div>

            <div class="col-6 mt-4">
              <label>Fecha(<span class="text-danger">*</span>)</label>
              <soft-input
                id="usuario"
                type="time"
                placeholder=""
                name="surname"
                size=""
              />
            </div>


              <div class="col-6 mt-4">
              <label>Dia(<span class="text-danger">*</span>)</label>
              <VueMultiselect
                  v-model="trip.day" 
                  :options="options" 
                  :multiple="true" 
                  :close-on-select="false" 
                  :clear-on-select="false" 
                  :preserve-search="true" 
                   placeholder="Pick some"
                   label="name" 
                   track-by="name" 
                   :preselect-first="false"
                   @select="change"
                   >
                  <template #noResult>
                    Elemento no encontrado
                  </template>
                </VueMultiselect>
            </div>


          </div>
          <div class="row mt-4">
            <div class="col-12 text-end ">
              <soft-button
                type="button"
                color="secondary"
                variant="gradient"
                class="mb-0 me-2 ms-auto js-btn-next"
                title="Next"
                @click="this.$parent.nextStep"
                >Cancelar</soft-button>
              <soft-button
                type="button"
                color="dark"
                variant="gradient"
                class="mb-0 ms-auto js-btn-next"
                title="Next"
                @click="this.$parent.nextStep"
                >Crear Viaje</soft-button>
            </div>
          </div>
      </div>
    </div>
  </div>
  </div>
</template>

<script>
import Quill from "quill";
import Choices from "choices.js";
import SoftInput from "@/components/SoftInput.vue";
import SoftButton from "@/components/SoftButton.vue";
import VueMultiselect from 'vue-multiselect'
export default {
  name: "NewTravel",
  components: {
  //  Multiselect,
    SoftInput,
    SoftButton,
    VueMultiselect 
  },
  data() {
    return {
      showMenu: false,
      activeClass: "js-active position-relative",
      activeStep: 0,
      formSteps: 3,
      trip:{
        driver: "",
        bus: "",
        route: "",
        startDate: "",
        day: "",
      },
      options: [
        { name: 'Domingo', value: 1 },
        { name: 'Lunes', value: 2 },
        { name: 'Martes', value: 3 },
        { name: 'Miercoles', value: 4},
        { name: 'Jueves', value: 5 },
        { name: 'Viernes', value: 6 },
      ]
    };
  },
  mounted() {
    if (document.getElementById("edit-description")) {
      // eslint-disable-next-line no-unused-vars
      var quill = new Quill("#edit-description", {
        theme: "snow", // Specify theme in configuration
      });
    }
    if (document.getElementById("choices-category")) {
      var element = document.getElementById("choices-category");
      new Choices(element, {
        searchEnabled: false,
      });
    }

    if (document.getElementById("choices-route")) {
      let element = document.getElementById("choices-route");
      new Choices(element, {
        searchEnabled: false,
      });
    }

     if (document.getElementById("choices-bus")) {
      let element = document.getElementById("choices-bus");
      new Choices(element, {
        searchEnabled: false,
      });
    }

     if (document.getElementById("choices-service")) {
      let element = document.getElementById("choices-service");
      new Choices(element, {
        searchEnabled: false,
      });
    }
  },
  methods: {
    nextStep() {
      if (this.activeStep < this.formSteps) {
        this.activeStep += 1;
      } else {
        this.activeStep -= 1;
      }
    },
    prevStep() {
      if (this.activeStep > 0) {
        this.activeStep -= 1;
      }
      
    },
    change(event){
      console.log(event);
      console.log(this.trip.day);
    }
  },
};
</script>

<style src="vue-multiselect/dist/vue-multiselect.css"></style>